input[type=range] {
  height: 20px;
  -webkit-appearance: none;
  margin: 4px 0;
  width: 100%;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 10px;
  cursor: pointer;
  //animate: 0.2s;
  box-shadow: 1px 1px 1px #717882;
  background: #50555C;
  border-radius: 14px;
  border: 1px solid #9C9C9C;
}
input[type=range]::-webkit-slider-thumb {
  box-shadow: 2px 2px 1px #000000;
  border: 0px solid #000000;
  height: 33px;
  width: 33px;
  border-radius: 33px;
  background: #529DE1;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -6.5px;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background: #50555C;
}
input[type=range]::-moz-range-track {
  width: 100%;
  height: 10px;
  cursor: pointer;
  //animate: 0.2s;
  box-shadow: 1px 1px 1px #717882;
  background: #50555C;
  border-radius: 14px;
  border: 1px solid #9C9C9C;
}
input[type=range]::-moz-range-thumb {
  box-shadow: 2px 2px 1px #000000;
  border: 0px solid #000000;
  height: 33px;
  width: 33px;
  border-radius: 33px;
  background: #529DE1;
  cursor: pointer;
}
input[type=range]::-ms-track {
  width: 100%;
  height: 10px;
  cursor: pointer;
  //animate: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: #50555C;
  border: 1px solid #9C9C9C;
  border-radius: 28px;
  box-shadow: 1px 1px 1px #717882;
}
input[type=range]::-ms-fill-upper {
  background: #50555C;
  border: 1px solid #9C9C9C;
  border-radius: 28px;
  box-shadow: 1px 1px 1px #717882;
}
input[type=range]::-ms-thumb {
  margin-top: 1px;
  box-shadow: 2px 2px 1px #000000;
  border: 0px solid #000000;
  height: 33px;
  width: 33px;
  border-radius: 33px;
  background: #529DE1;
  cursor: pointer;
}
input[type=range]:focus::-ms-fill-lower {
  background: #50555C;
}
input[type=range]:focus::-ms-fill-upper {
  background: #50555C;
}
